import React, { useEffect, useState } from "react";
import axios from "axios";
import { Bar, Line, Pie } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  ArcElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  ArcElement,
  Title,
  Tooltip,
  Legend
);
import CardDataStats from "./dasboardCardData";

interface User {
  id: number;
  name: string;
  role: string;
}

interface Technology {
  id: number;
  name: string;
}

interface Company {
  id: number;
  name: string;
}

interface Location {
  id: number;
  name: string;
}

interface Client {
  id: number;
  name: string;
}

interface Certification {
  id: number;
  name: string;
  candidateId: number;
}

const Dashboard: React.FC<{ userRole: string }> = ({ userRole }) => {
  const [users, setUsers] = useState<User[]>([]);
  const [technologies, setTechnologies] = useState<Technology[]>([]);
  const [companies, setCompanies] = useState<Company[]>([]);
  const [locations, setLocations] = useState<Location[]>([]);
  const [clients, setClients] = useState<Client[]>([]);
  const [certifications, setCertifications] = useState<Certification[]>([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const usersResponse = await axios.get("/api/users/all");
        setUsers(usersResponse.data);

        const technologiesResponse = await axios.get("/api/technologies");
        setTechnologies(technologiesResponse.data);

        const companiesResponse = await axios.get("/api/master-company");
        setCompanies(companiesResponse.data);

        const locationsResponse = await axios.get("/api/locations");
        setLocations(locationsResponse.data);

        const clientsResponse = await axios.get("/api/clients");
        setClients(clientsResponse.data);

        const certificationsResponse = await axios.get("/api/certifications");
        setCertifications(certificationsResponse.data);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, []);

  // // Bar Chart Data
  // const barChartData = {
  //   labels: technologies.map(tech => tech.name),
  //   datasets: [
  //     {
  //       label: 'Technologies',
  //       data: technologies.map(tech => tech.id), // Replace with actual data
  //       backgroundColor: 'rgba(75, 192, 192, 0.2)',
  //       borderColor: 'rgba(75, 192, 192, 1)',
  //       borderWidth: 1,
  //     },
  //   ],
  // };

  // // Line Chart Data
  // const lineChartData = {
  //   labels: companies.map(company => company.name),
  //   datasets: [
  //     {
  //       label: 'Candidates by Company',
  //       data: companies.map(company => company.id), // Replace with actual data
  //       borderColor: 'rgba(153, 102, 255, 1)',
  //       borderWidth: 2,
  //       fill: false,
  //     },
  //   ],
  // };

  // // Pie Chart Data
  // const pieChartData = {
  //   labels: certifications.map(cert => cert.name),
  //   datasets: [
  //     {
  //       label: 'Certifications',
  //       data: certifications.map(cert => cert.candidateId), // Replace with actual data
  //       backgroundColor: [
  //         'rgba(255, 99, 132, 0.2)',
  //         'rgba(54, 162, 235, 0.2)',
  //         'rgba(255, 206, 86, 0.2)',
  //         'rgba(75, 192, 192, 0.2)',
  //         'rgba(153, 102, 255, 0.2)',
  //       ],
  //       borderColor: [
  //         'rgba(255, 99, 132, 1)',
  //         'rgba(54, 162, 235, 1)',
  //         'rgba(255, 206, 86, 1)',
  //         'rgba(75, 192, 192, 1)',
  //         'rgba(153, 102, 255, 1)',
  //       ],
  //       borderWidth: 1,
  //     },
  //   ],
  // };

  // const chartOptions = {
  //   responsive: true,
  //   plugins: {
  //     legend: {
  //       position: 'top' as const,
  //     },
  //     title: {
  //       display: true,
  //       text: 'Dashboard Analytics',
  //     },
  //   },
  // };

  return (
    <div className="p-6 bg-gray-100 min-h-screen">
      <h1 className="text-2xl font-bold mb-6">Welcome, {userRole}</h1>

      {/* Admin-Specific Content */}

      <CardDataStats title="Total Users" total={users.length}>
        <svg
          className="fill-primary-500"
          width="22"
          height="18"
          viewBox="0 0 22 18"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M7.18418 8.03751C9.31543 8.03751 11.0686 6.35313 11.0686 4.25626C11.0686 2.15938 9.31543 0.475006 7.18418 0.475006C5.05293 0.475006 3.2998 2.15938 3.2998 4.25626C3.2998 6.35313 5.05293 8.03751 7.18418 8.03751ZM7.18418 2.05626C8.45605 2.05626 9.52168 3.05313 9.52168 4.29063C9.52168 5.52813 8.49043 6.52501 7.18418 6.52501C5.87793 6.52501 4.84668 5.52813 4.84668 4.29063C4.84668 3.05313 5.9123 2.05626 7.18418 2.05626Z"
            fill=""
          />
          <path
            d="M15.8124 9.6875C17.6687 9.6875 19.1468 8.24375 19.1468 6.42188C19.1468 4.6 17.6343 3.15625 15.8124 3.15625C13.9905 3.15625 12.478 4.6 12.478 6.42188C12.478 8.24375 13.9905 9.6875 15.8124 9.6875ZM15.8124 4.7375C16.8093 4.7375 17.5999 5.49375 17.5999 6.45625C17.5999 7.41875 16.8093 8.175 15.8124 8.175C14.8155 8.175 14.0249 7.41875 14.0249 6.45625C14.0249 5.49375 14.8155 4.7375 15.8124 4.7375Z"
            fill=""
          />
          <path
            d="M15.9843 10.0313H15.6749C14.6437 10.0313 13.6468 10.3406 12.7874 10.8563C11.8593 9.61876 10.3812 8.79376 8.73115 8.79376H5.67178C2.85303 8.82814 0.618652 11.0625 0.618652 13.8469V16.3219C0.618652 16.975 1.13428 17.4906 1.7874 17.4906H20.2468C20.8999 17.4906 21.4499 16.9406 21.4499 16.2875V15.4625C21.4155 12.4719 18.9749 10.0313 15.9843 10.0313ZM2.16553 15.9438V13.8469C2.16553 11.9219 3.74678 10.3406 5.67178 10.3406H8.73115C10.6562 10.3406 12.2374 11.9219 12.2374 13.8469V15.9438H2.16553V15.9438ZM19.8687 15.9438H13.7499V13.8469C13.7499 13.2969 13.6468 12.7469 13.4749 12.2313C14.0937 11.7844 14.8499 11.5781 15.6405 11.5781H15.9499C18.0812 11.5781 19.8343 13.3313 19.8343 15.4625V15.9438H19.8687Z"
            fill=""
          />
        </svg>
      </CardDataStats>

      {/* User-Specific Content */}
      {userRole === "admin" && (
        <div className="mb-8">
          <h2 className="text-xl font-semibold mb-4">Users</h2>
          <ul className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {users.map((user) => (
              <li key={user.id} className="bg-white p-4 rounded-lg shadow">
                {user.name} - <span className="text-gray-600">{user.role}</span>
              </li>
            ))}
          </ul>
        </div>
      )}

      {userRole === "recruiter" && (
        <div className="mb-8">
          <h2 className="text-xl font-semibold mb-4">Candidates by Company</h2>
          <ul className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {companies.map((company) => (
              <li key={company.id} className="bg-white p-4 rounded-lg shadow">
                {company.name}
              </li>
            ))}
          </ul>
        </div>
      )}

      {userRole === "candidate" && (
        <div className="mb-8">
          <h2 className="text-xl font-semibold mb-4">Available Technologies</h2>
          <ul className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {technologies.map((tech) => (
              <li key={tech.id} className="bg-white p-4 rounded-lg shadow">
                {tech.name}
              </li>
            ))}
          </ul>
        </div>
      )}

      {/* Charts Section */}
      <h2 className="text-xl font-semibold mb-4">Statistics</h2>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
        <div className="bg-white p-4 rounded-lg shadow">
          <h3 className="text-lg font-semibold mb-2">
            Technologies Distribution (Bar Chart)
          </h3>
          {/* <Bar data={barChartData} options={chartOptions} /> */}
        </div>
        <div className="bg-white p-4 rounded-lg shadow">
          <h3 className="text-lg font-semibold mb-2">
            Candidates by Company (Line Chart)
          </h3>
          {/* <Line data={lineChartData} options={chartOptions} /> */}
        </div>
        <div className="bg-white p-4 rounded-lg shadow">
          <h3 className="text-lg font-semibold mb-2">
            Certifications (Pie Chart)
          </h3>
          {/* <Pie data={pieChartData} options={chartOptions} /> */}
        </div>
      </div>

      {/* Shortcuts Section */}
      <h2 className="text-xl font-semibold mb-4">Shortcuts</h2>
      <div className="flex flex-wrap gap-4">
        <button
          className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
          onClick={() => alert("Navigate to Users")}
        >
          Users
        </button>
        <button
          className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
          onClick={() => alert("Navigate to Technologies")}
        >
          Technologies
        </button>
        <button
          className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
          onClick={() => alert("Navigate to Companies")}
        >
          Companies
        </button>
        <button
          className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
          onClick={() => alert("Navigate to Locations")}
        >
          Locations
        </button>
        <button
          className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
          onClick={() => alert("Navigate to Clients")}
        >
          Clients
        </button>
        <button
          className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
          onClick={() => alert("Navigate to Certifications")}
        >
          Certifications
        </button>
      </div>
    </div>
  );
};

export default Dashboard;
